const createGalleryCardTemplate = pictureInfo => {
    return `
    <li class="gallery-item">
	    <a class="gallery-link" href="${pictureInfo.original}">
		    <img
			    class="gallery-image"
			    src="${pictureInfo.preview}"
			    alt="${pictureInfo.description}"
			/>
	    </a>
    </li>`
}
const galleryCardsTemplate = images.map(imgInfo => createGalleryCardTemplate(imgInfo)).join('');

document.querySelector('.gallery').innerHTML = galleryCardsTemplate;

const lightbox = new simpleLightbox('.gallery a', {
    captionsData: 'alt',
});